.App {
  text-align: center;
}
body {
  margin:0;
  padding:0;
  font-family: Arial, Helvetica, sans-serif;
}
.navbar{
  width:100%;
  height:80px;
  background-color:cadetblue;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.navbar .links{
  text-align: center;
  margin-right: 50px;
}
.links a{
  color: whitesmoke;
  text-decoration: none;
  border-bottom: 3px solid white;
  padding-bottom: 2px;
  margin:10px;
}
.navbar .user{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right:50px;
}
.user p{
  font-size: 15px;
  margin-right: 10px;
  color:whitesmoke;
}
.user img{
  font-size: 15px;
  border-radius: 50%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.create-post{
  width:100%;
  height:auto;
  display: flex;
  justify-content: center;
}
form{
  margin-top: 50px;
  padding: 30px 20px;
  width:320px;
  border-radius: 7px;
  background-color: white;
  backdrop-filter: blur(5px);
  background-color: rgba(43,0,255,0.588);
  overflow: hidden;
}

input,textarea{
  padding:8px 10px;
  margin:3px 8px 16px 8px;
  background-color: rgba(222,239,248,0.877);
  border: 0px transparent;
  border-radius: 5px;
  color: rgb(97,4,184);
  font-size:16px;
}
.submitForm:hover{
  cursor: pointer;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
